.persons-select-container {
  width: auto;
  min-width: 300px;
  display: inline-block;
  a {
    color: black;
    &:hover {
      //nefunguje
      cursor: pointer;
      color: blue;
    }
  }
  .person-container {
    height: 100%;
    display: flex;
    align-items: center;
    img {
      height: 100%;
    }
    .person-details {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: center;
    }
  }

  input {
    box-shadow: none;
    border: none;
  }
  img {
    &:hover {
      cursor: pointer;
    }
  }
}

.menu-buttons-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  .checkboxes-container {
    width: 100%;
    // border: 1px solid;
    margin: 10px 0px;
    display: flex;
    align-items: center;
    justify-content: right;
  }
  button {
    display: inline-block;
    width: auto;
    height: 60%;
    margin-left: 3px;
    white-space: nowrap;
    &:hover {
      cursor: pointer;
    }
  }
}
.checkbox_nezar-container {
  box-sizing: border-box;
  width: 100%;
  display: flex;
  justify-content: right;
  padding-right: 2px;
  // background-color: grey;
}
