.unit-select-container {
  margin-top: 5px;
  margin-bottom: 5px;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  border-color: rgb(204, 204, 204);
  border-radius: 4px;
  border-style: solid;
  border-width: 1px;
  box-sizing: border-box;
  .input-container {
    -webkit-box-align: center;
    align-items: center;
    display: flex;
    flex: 1 1 0%;
    flex-wrap: wrap;
    padding: 0px 8px;
    position: relative;
    overflow: hidden;
    box-sizing: border-box;
    .unit-container-input {
      display: flex;
      flex-direction: row;
      align-items: center;
      // padding: 0px 0px 0px 5px;
      margin: 2px 0px 2px 8px;
      background-color: rgb(230, 230, 230);
      border-radius: 2px;
      box-sizing: border-box;
      .clear-indicator-unit-container {
        border-radius: 2px;
        cursor: default;
        box-sizing: border-box;
        opacity: 0.2;
        &:hover {
          background-color: rgb(234, 157, 102);
          font-weight: bold;
          opacity: 0.9;
        }
        svg {
          // vertical-align: center;
          //nedari se mi nastavit mensi rozmery
          //padding-top: 3px;
          // padding: 1px;
          // margin: 1px;
          // padding-right: 3px;
          // margin-bottom: 3px;
          // margin-right: 20px;
          // color: yellow;//u svg nutne pouzit fill
          // fill: red;
          height: 18px;
          width: 18px;
          transform: scale(0.8);
          &:hover {
            fill: red;
          }
        }
      }
    }
  }
}

.indicators-container {
  -webkit-box-align: center;
  align-items: center;
  align-self: stretch;
  display: flex;
  flex-shrink: 0;
  box-sizing: border-box;
  svg {
    width: 20px;
    height: 20px;
  }
  .clear-indicator {
    display: flex;
    padding: 3px;
    box-sizing: border-box;
    opacity: 0.2;
    transition: opacity 150ms ease 0s;
    &:hover {
      opacity: 0.4;
    }
  }
  .indicator-separator {
    align-self: stretch;
    background-color: rgb(204, 204, 204);
    margin-bottom: 8px;
    margin-top: 8px;
    width: 1px;
    box-sizing: border-box;
  }
  .dropdown-indicator {
    color: rgb(204, 204, 204);
    display: flex;
    padding: 3px;
    opacity: 0.2;
    transition: opacity 150ms ease 0s;
    &:hover {
      opacity: 0.4;
    }
  }
}

.popup-container {
  overflow-y: auto;
  padding: 0px 8px;
  position: relative;
  box-sizing: border-box;
  text-align: left;
  border-color: #4e5458;
  color: #181a1b;
  .unit-container-menu {
    display: inline-block;
    border-radius: 2px;
    margin: 10px 0px 5px 8px;
    box-sizing: border-box;
    padding: 4px 2px 4px 2px;
    &:hover {
      cursor: default;
      background-color: #999;
      color: white;
    }
  }
}
.unit-circle {
  height: 10px;
  width: 10px;
  border-radius: 50%;
  display: inline-block;
  padding: 0px;
  margin-left: 4px;
  margin-right: 4px;
}
